import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import {EventBus} from './event-bus.js';

Vue.use(Router)

var router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [{
        path: '/',
        name: 'home',
        component: Home
    }, {
        path: '/plans',
        name: 'plans',
        // eslint-disable-next-line
        component: () => import(/* webpackChunkName: "about" */ './views/Plans.vue')
    }, {
        path: '/services',
        name: 'services',
        // eslint-disable-next-line
        component: () => import(/* webpackChunkName: "about" */ './views/Services.vue')
    }, {
        path: '/contact',
        name: 'contact',
        // eslint-disable-next-line
        component: () => import(/* webpackChunkName: "about" */ './views/Contact.vue')
    }]
});

router.beforeEach((to, from, next) => {
    if (typeof shrinkBody !== 'undefined' && shrinkBody) {
        EventBus.$emit('component-click-menu-icon');
        EventBus.$emit('component-route-fade-out');
        setTimeout(() => {
            window.requestAnimationFrame(() => {
                EventBus.$emit('component-route-fade-in');
                next();
                EventBus.$emit('component-scroll-page-to-top');
            });
        }, 600);
    } else {
        EventBus.$emit('component-route-fade-out');
        setTimeout(() => {
            window.requestAnimationFrame(() => {
                EventBus.$emit('component-route-fade-in');
                next();
                EventBus.$emit('component-scroll-page-to-top');
            });
        }, 250);
    }
    
})

export default router;
