<style lang="scss">
@import "./styles/global.scss";
</style>

<template>
    <div id="app" 
        :class="{'modifier--logo-animation-complete': canShowBody, 'modifier--is-touch-app': isMobile}">
        <div id="logo-wrapper">
            <Logo />
        </div>
        
        <div id="menu-wrapper">
            <Menu />
        </div>

        <transition name="fade">
            <perfect-scrollbar 
                :swipeEasing="true"
                :class="[{'modifier--shrink': shrinkBody, 'modifier--fade-out': routeFaded}, routeClass]"
                id="main"
                ref="main"
                @click="clickMenuClose"
                v-if="isMobile !== true && canShowBody">
                    <router-view 
                        class="router-view modifier--desktop-router-view" />
            </perfect-scrollbar>
        </transition>

        <transition name="fade">
            <router-view
                id="main"
                ref="main" 
                @click="clickMenuClose"
                :class="[{'modifier--shrink': shrinkBody, 'modifier--fade-out': routeFaded}, routeClass]"
                class="router-view modifier--mobile-router-view" 
                v-if="isMobile && canShowBody"/>
        </transition>
    </div>
</template>

<script>
    import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
    import 'vue2-perfect-scrollbar/dist/vue2-perfect-scrollbar.css'

    import {EventBus} from '@/event-bus';
    import Logo from "@/components/Logo";
    import Menu from "@/components/Menu";
    export default {
        name: 'app',
        
        components: {
            Logo,
            Menu,
            PerfectScrollbar
        },
        
        data(){
            return {
                canShowBody: false,
                shrinkBody: false,
                isMobile: true,
                routeClass: '',
                routeFaded: false
            }
        },

        methods: {
            fadeOutRoute() {
                this.routeFaded = true;
            },

            fadeInRoute(){
                this.routeFaded = false;
            },

            showBodyContent(){
                this.canShowBody = true;
                if (this.isMobile !== true) {
                    this.$nextTick(() => {
                        this.appendScrollEvents();
                    });
                }
            },

            changeBodySize(val){
                this.shrinkBody = val;
                window.shrinkBody = val;
            },

            clickMenuClose(){
                if (this.shrinkBody) {
                    EventBus.$emit('component-click-menu-icon');
                }
            },

            updateScroll(){
                try {
                    this.$refs.main.ps.update();
                } catch(e){
                    // eslint-disable-next-line
                    console.warn(e);
                }
            },

            appendScrollEvents(){
                const $main = this.$refs.main;
                if ($main && $main.$el) {
                    window.fancyScrollElement = $main.$el;
                    $main.$el.addEventListener('ps-scroll-down', () => {
                        var ps = $main.ps;
                        if (ps && ps.lastScrollTop > window.innerHeight - 80) {
                            // console.log('banner not in view')
                        }
                    });

                    $main.$el.addEventListener('ps-scroll-up', () => {
                        var ps = $main.ps;
                        if (ps && ps.lastScrollTop <= window.innerHeight - 80) {
                            // console.log('banner in view')
                        }
                    })
                }
            },

            scrollPageToTop(){
                if (this.$refs.main) {
                    this.$refs.main.$el.scrollTop = 0;
                }
            },

            scrollElementToView(element){
                element = element || window.fancyScrollElement;
                var duration = 400;
                
                if (!element) {
                    return;
                }

                var startingTop = element.offsetParent.scrollTop;
                var elementTop = element.offsetTop;
                var dist = elementTop - startingTop;
                var start;

                window.requestAnimationFrame(function step(timestamp) {
                    if (!start)
                        start = timestamp;
                    var time = timestamp - start;
                    var percent = Math.min(time / duration, 1);
                    element.offsetParent.scrollTo(0, startingTop + dist * percent);

                    // Proceed with animation as long as we wanted it to.
                    if (time < duration) {
                        window.requestAnimationFrame(step);
                    }
                })
            }
        },

        watch: {
            $route(to){
                try {
                    this.routeClass = 'route-' + to.name.split(' ').join('-');
                } catch(e){
                    this.routeClass = '';
                }
            }
        },

        created(){
            EventBus.$on('bus-show-body-content', this.showBodyContent);
            EventBus.$on('bus-shrink-body', this.changeBodySize);
            EventBus.$on('bus-update-scroll', this.updateScroll);
            EventBus.$on('bus-route-fade-out', this.fadeOutRoute);
            EventBus.$on('bus-route-fade-in', this.fadeInRoute);
            EventBus.$on('bus-scroll-to-element', this.scrollElementToView);
            EventBus.$on('bus-scroll-page-to-top', this.scrollPageToTop);

            /**
            * Test For CSS3 property support
            * use 'perspective' to test for 3d support
            */
            var supports = (function() {

                'use strict';

                var div = document.createElement('div'),
                    vendors = 'Khtml ms O Moz Webkit'.split(' '),
                    len = vendors.length;

                return function(prop) {

                    if (prop in div.style) return true;

                    prop = prop.replace(/^[a-z]/, function(val) {
                        return val.toUpperCase();
                    });

                    while(len--) {
                        if (vendors[len] + prop in div.style) {
                            return true;
                        } 
                    }

                    return false;
                };
            })();

            // this.isMobile = supports('perspective') || false
            this.isMobile = 'ontouchstart' in window;
            try {
                this.routeClass = 'route-' + this.$router.currentRoute.name;
            } catch(e){
                this.routeClass = '';
            }
        }
    }
</script>
